}

.finish__view__submit__line {
  margin: 0;
}
.finish__view__submit__line__strip {
  background-color: #d5d5d5;
  position: relative;
  top: 3.2rem;
  z-index: 0;
  height: 1.6rem;
  width: 100%;
}
.finish__view__submit__line__button {
  margin: 1rem;
  padding: 0.8rem 1.6rem;
  z-index: 1;
  position: relative;
}

.cart__column {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  max-height: 100vh;
  overflow-y: auto;
}

.order__form__submit__line {
  margin: 0;
  margin-bottom: 1rem;
  text-align: center;
  position: -webkit-sticky;
  position: sticky;
  bottom: 1rem;
}
.mobile__cart__view .order__form__submit__line {
  position: fixed;
  bottom: 0;
  width: 100%;
}
.order__form__submit__line__strip {
  background-color: #d4d4d4;
  position: relative;
  top: 3.2rem;
  z-index: 0;
  height: 1.6rem;
  width: 100%;
}
.order__form__submit__line__button {
  margin: 1rem;
  padding: 0.8rem 1.6rem;
  z-index: 1;
  position: relative;
  -webkit-font-feature-settings: "smcp", "c2sc";
          font-feature-settings: "smcp", "c2sc";
  font-variant-caps: all-small-caps;
}

.shop__header__icons__doveze {
  margin: 0.6rem 0 auto 0;
  height: 1.2rem;
}
.shop__header__icons__gopay {
  float: right;
  height: 2.5rem;
}
.shop__header__icons__commerce {
  float: right;
  width: 2rem;
  height: 2.5rem;
  margin-left: 0.5rem;
  margin-right: 0.8rem;
}
@media only screen and (max-width: 340px) {
  .shop__header__icons__gopay {
    display: none;
  }
}
@media only screen and (max-width: 250px) {
  .shop__header__icons__commerce {
    display: none;
  }
}

.order__method {
  padding: 0.2rem;
}

.order__method button {
  margin: 0.3rem 1rem 0.3rem 1rem;
  vertical-align: middle;
  padding: 0.3rem 1.2rem;
  height: 2.7rem;
}

.order__method button span:before,
.order__method button span:after {
  margin: 0 0.8rem 0 0;
  font-size: 1.5rem;
  vertical-align: middle;
  font-weight: normal;
  font-weight: initial;
}

.delivery__map__address {
  position: absolute;
  top: 0;
  left: 2.3rem;
  right: 2.3rem;
}
.delivery__map__submit {
  position: absolute;
  bottom: 3rem;
  width: 100%;
}

.address__input__box .input-group-addon {
  background: transparent;
  border: none;
  float: right;
}
.address__input__box .input-group-addon {
  background-color: transparent;
  color: #99230e;
}
.address__input__box .input-group-addon:hover {
  color: #b22810;
}
.address__input__box .flaticon-gps:before {
  margin-left: 0rem;
}
.address__input__box .input-group-btn {
  margin-left: 0.7rem;
}
.address__input .alert {
  margin-top: 0.3rem;
  border-radius: 1rem;
}
.address__input__box__resolved {
  margin: 0.4rem 0.7rem 0.4rem 0.7rem;
  font-size: 1rem;
  color: green;
  width: 100%;
}
.address__input__box__resolved.wait {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 0.5rem;
  cursor: wait;
}
.address__input__hidden {
  display: none;
}

.button__stripe {
  margin: 0;
}
.button__stripe__bar {
  background-color: #d4d4d4;
  position: relative;
  top: 3.3rem;
  z-index: 0;
  height: 1.6rem;
  width: 100%;
  pointer-events: visible;
}
.button__stripe__button {
  z-index: 1;
  position: relative;
  top: 1rem;
  text-align: center;
}
.button__stripe__button button {
  padding: 0.8rem 1.6rem;
}
.button__stripe__button button.disabled {
  background-color: #d4d4d4;
  opacity: 1;
  border: 0;
}
.button__stripe__button button.disabled:hover {
  background-color: #d4d4d4;
}

a.category__navigator__scroll__link {
  text-align: center;
  color: #595959 !important;
  display: inline-block;
  min-width: 4rem;
